import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Cronograma_auditoria } from '../../../modelo/Cronograma_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'cronograma_auditoria-dados-edit',   templateUrl: 'cronograma_auditoria_dadosEdit.component.html' }) export class Cronograma_auditoriaDadosEditComponent implements OnInit {    cronograma_auditoria: Cronograma_auditoria;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.cronograma_auditoria.Cronograma_auditoria";                 let id = +params['id'];         this.cronograma_auditoria = new Cronograma_auditoria();         this.cronograma_auditoria.cd_cronograma_auditoria = id;         o.objetoJson = this.cronograma_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a cronograma_auditoria. ';       }      this.cronograma_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


